.spaceOverlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left:0;
  background: cornflowerblue;
}

.spaceShape {
  fill: gold;
  -webkit-box-shadow: inset 0px 0px 27px -1px rgba(0,0,0,0.75);
          box-shadow: inset 0px 0px 27px -1px rgba(0,0,0,0.75);
}

.breathe {
  -webkit-animation: breathe 3s infinite ease-in-out;
          animation: breathe 3s infinite ease-in-out;
}

.titleText {
  font-size: 150px;
  letter-spacing: 260px;
}

.secondText {
  font-size: 127px;
    letter-spacing: 45px;
}

.thirdText {
  font-size: 127px;
  letter-spacing: 45px;
}

@-webkit-keyframes breathe{
  50%{
    -webkit-transform: scaleY(1.03);
            transform: scaleY(1.03);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

@keyframes breathe{
  50%{
    -webkit-transform: scaleY(1.03);
            transform: scaleY(1.03);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}


.countdown {
  position: absolute;
  top: 28%;
  left: 31%;
  width: 240px;
  -webkit-transform: rotateZ(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotateZ(-90deg);
}

.clockText {
  font-size: 70px;
}


